@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Open Sans', sans-serif}body,#root,#root>div{min-height:80vh}a{text-decoration:none;color:black}aa:visited{color:black}button{border:none;outline:none;color:white;background-color:darkslateblue;padding:10px 20px;border-radius:5px}input[type='text']{font-size:1rem;padding:4px 2px;border-radius:5px;border:1px solid darkblue;outline:none}input[type='submit']{border-radius:5px;background-color:darkblue;color:white;padding:5px 20px}.container{width:90%;margin:auto}.container.flex{flex-direction:column;align-items:unset}.flex{display:flex;justify-content:space-between;align-items:center}.search-bar{margin:30px 0}.search-bar input[type='text']{margin:0 10px;width:100%}.search-bar button{padding:5px 10px;background-color:white;font-size:1.8rem;color:darkblue}.search-bar .back-button{font-size:2.1rem}.main-categories{padding:30px 30px;border-bottom:1px solid darkblue}.personal-categories{padding:30px 30px}.personal-categories .category{font-size:1.7rem}.category{font-weight:700;font-size:2rem}.category .category__item-count{margin-left:1rem;font-weight:400;color:darkblue}.add-bar{position:fixed;bottom:0;left:0;background-color:white;width:100%;padding:20px}.add-bar.flex{flex-direction:row;align-items:center;justify-content:flex-end}.add-bar .button__add-todo{border-radius:50%;padding:0px;width:60px;height:60px;font-size:2rem;box-shadow:2px 4px 18px 5px rgba(0,0,0,0.05)}.todos{padding:30px 0 120px 0}.todo{margin-bottom:1px;font-size:1.2rem;padding:10px 10px;border-bottom:1px solid rgba(72,64,139,0.5)}.todo.todo--completed .todo-text{text-decoration:line-through;opacity:0.4}.todo button{padding:0;background-color:white}.todo.todo--light{background-color:white}.todo .todo-text{border-radius:0;margin:0 8px;opacity:0.8}.todo .todo-text--light{background-color:white}.todo textarea{width:100%;font-size:1.26rem;border-radius:10px;border:none;resize:none;font-family:'Raleway', sans-serif;outline:none}.new-todo{min-height:100vh;font-weight:700}.new-todo.flex{flex-direction:column;justify-content:center;align-items:flex-start}.new-todo input{width:100%;margin:10px 0}.new-todo .form-content{border:1px solid darkblue;border-radius:5px;padding:10px;flex-direction:column;align-items:flex-start;width:100%;box-shadow:2px 4px 18px 5px rgba(0,0,0,0.12)}.new-todo select{margin:10px 0;border:1px solid darkblue;font-size:1rem;padding:4px 2px;outline:none;width:100%;border-radius:5px}.new-todo div{justify-content:space-between;width:100%}

